import React from 'react'
import {connect} from 'react-redux'
import * as R from 'ramda'
import {
    getBasketPhonesWithCount,
    getTotalBasketPrice
} from '../../selectors.js'
import {
    sendOrder,
  clearBasket
} from '../../actions/index.js'

const Order = ({phones, sendOrder, clearBasket}) => {
    return (
        <div className='hidden modal' id='order'>
            <div className="login-page">
                <div className="form">
                    <button
                        className="cancel-btn"
                        onClick={() => {
                            document.getElementById('order').classList.add('hidden')
                        }}>
                        <div className='cancel-text'>+</div>
                    </button>
                    <div className="login-form">
                        <input id='1' type="text" placeholder="ім'я"/>
                        <input id='2' type='tel' placeholder='номер телефону'/>
                        <input  id = '3' type="email" placeholder="пошта"/>
                        <button className='btn-buy' onClick={()=>{
                            sendOrder(
                                document.getElementById('1').value,
                                document.getElementById('3').value,
                                document.getElementById('2').value,
                                R.compose(
                                    R.fromPairs,
                                    R.map(x =>  [`products[${x.id}]`, x.count])
                                )(phones)
                            )
                            const ids = R.compose(
                                R.uniq,
                                R.pluck('id')
                            )(phones)
                            clearBasket(ids)
                            document.getElementById('order').classList.add('hide');
                        }

                        }>Купити</button>
                    </div>
                </div>
            </div>
        </div>
    )
}


const mapStateToProps = state => {
    return {
        phones: getBasketPhonesWithCount(state),
        totalPrice: getTotalBasketPrice(state)
    }
}
const mapDispatchToProps = {
    sendOrder,
    clearBasket
}
export default connect(mapStateToProps, mapDispatchToProps)(Order)
